.input-color {
    position: relative;
    display: inline-block;
    height: 20px;
    width: 20px;
    vertical-align: middle;

    .input-overlay { 
        pointer-events: none;
        cursor: pointer;
        z-index: 1;
        border-radius: 100%;
        border: 2px solid $primaryColor;
    }

    input { 
        z-index: 0; 
        opacity: 0.1;
    }

    .input-overlay, &:before, input {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        cursor: pointer;
    }
}

.select-container {
    position: relative;
    height: 30px;
    display: inline-block;
    width: auto;
    color: $primaryColor;
    border-bottom: 1px solid;
    vertical-align: middle;
    font-size: 14pt;

    > * {
        vertical-align: top;
    }

    label {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        line-height: 30px;
    }

    select {
        -webkit-appearance: none;
        appearance: none;
        background: none;
        border: none;
        color: $primaryColor;
        padding: 0 15px 0 60px;
        height: 100%;
        line-height: 30px;
        font-size: 12pt;
        cursor: pointer;
    }

    &:after {
        content: '\f107';
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        position: absolute;
        top: 0;
        right: 0;
        display: inline-block;
        height: 100%;
        line-height: 30px;
        pointer-events: none;
        cursor: pointer;
    }
}

.custom-checkbox {
    position: relative;
    height: 50px;
    width: 100px;

    input, .custom-checkbox-overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        cursor: pointer;
    }

    .custom-checkbox-overlay {
        pointer-events: none;
        background: white;
        border: 1px solid;
        color: $primaryColor;
        border-radius: 25px;
        transition: background .5s;

        .custom-checkbox-circle {
            position: relative;
            left: 0;
            height: 100%;
            width: 50px;
            border-radius: 100%;
            background: $primaryColor;
            transform: scale(0.8);
            transition: left .5s;
        }
    }

    input:checked + .custom-checkbox-overlay {
        background: $primaryColor;

        .custom-checkbox-circle {
            background: white;
            left: calc(100% - 50px);
        }
    }
}